
.nav {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 66px;
	color: #999;
	z-index: 9999;
  font-weight: 500;
  font-size: 18px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
	transition: all .3s;
  background: hsla(0,0%,100%,.86);
  box-shadow: 0 0 25px #ccc;
}
.nav-content {
  width: 80%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.nav-content-ul {
  width: 128px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.nav-content-ul li {
  height: 66px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
}
.nav-content-ul li:hover {
  color: rgba(5, 147, 211, 1);
}
.github-style {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 66px;
  color: #999;
  text-decoration: none;
}
.github-style:hover {
  color: rgba(5, 147, 211, 1);
}
.headtrait {
  width: 130px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
  position: relative;
}
.headtrait span {
  margin-left: 15px;
}
.head-overflow {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  overflow: hidden;
}
.head-overflow img {
  width: 100%;
  height: 100%;
}
.head-overflow:hover::after {
  content: attr(data-title);
  position: absolute;
  left: -210%;
  bottom: -95%;
  min-width: 325px;
  padding: 7px;
  background: hsla(0,0%,100%,.86);
  box-shadow: 0 0 15px #ccc;
  color: #2c3e50;
  border-radius: 7px;
}
.head-overflow:hover::before {
  content: "";
  width: 0;
  height: 0;
  border: 7px solid hsla(0,0%,100%,.86);
  border-right: 7px solid transparent;
  border-bottom: 7px solid transparent;
  transform: rotate(45deg);
  position: absolute;
  left: 10%;
  bottom: -40%;
  z-index: 10;
}



